.act1 {
  width: 100%;
  position: relative;
  overflow-x: hidden;
  background: $blue;
  
  .header {
    .date {
      color: rgb(0, 0, 0);
      .icon {
        filter: invert(100%);
      }
    }

    .title {
      color: rgb(0, 0, 0);
    }

    .menu {
      button {
        color: rgb(0, 0, 0);
        border: 1.5px solid;
      }
      .icon-menu {
        .icon1 {
          background-color: rgb(0, 0, 0);
        }

        .icon2 {
          background-color: rgb(0, 0, 0);
        }
      }
    }
  }

  .ball-header {
    position: absolute;
    top: -130px;
    right: 30px;
    transform: translate(50%, -50%);
    transform-origin: top;
    background: rgb(244, 244, 244);
    width: 400px;
    height: 400px;
    border-radius: 50%;
    animation: ballGrow 25s ease-out infinite;
    transition: 5s ease-in-out;
    @media screen and (max-width: 600px) {
      width: 280px;
      height: 280px;
      top: -80px;
      right: 30px;
    }

    @keyframes ballGrow {
      0% {
        transform: translate(50%, -50%) scale(1);
      }
      60% {
        transform: translate(50%, -50%) scale(1.3);
      }
      100% {
        transform: translate(50%, -50%) scale(1);
      }
    }
  }
  .img-content {
    width: 100%;
    height: 100vh;
    @include AlignChild(row, center, center);
    z-index: 1;

    .img {
      height: 100%;
      width: 100%;
      position: relative;
      overflow: hidden;

      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }

    .title {
      position: absolute;
      h1 {
        font-size: 15rem;
        color: white;
        font-family: $fontTR;
        letter-spacing: 10px;
        display: flex;
        gap: 10px;
        text-shadow: 0 5px 10px rgb(85, 84, 84);
        mix-blend-mode: overlay;
        @media screen and (max-width: 1000px) {
          font-size: 10rem;
        }
        @media screen and (max-width: 500px) {
          font-size: 5rem;
        }
      }
    }
  }
  .actSlider-container {
    height: 100vh;
    position: relative;
    z-index: 10;
    display: flex;
    width: 100%;
    background: white;
  }

  .time-bar {
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 15;
    background: rgb(61, 60, 60);
    width: 5px;
    height: 3px;
    border-radius: 300px;
  }

  .control-night {
    position: absolute;
    bottom: 40px;
    left: 60px;
    display: flex;
    align-items: end;
    justify-content: end;
    height: 40px;
    width: 40px;
    z-index: 15;

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      transform: translateX(-25px);
    }
  }
  .actSlider {
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
    gap: 150px;
    transition: 0.5s ease-out;

    .act2-slide {
      // &:nth-child(1) {
      //   .slide {
      //     position: relative;

      //     .video-container {
      //       left: 700px;

      //       @media screen and (max-width: 900px) {
      //         left: 10px;
      //       }
      //     }
      //   }
      // }
      // &:nth-child(2) {
      //   .slide {
      //     position: relative;

      //     .video-container {
      //       left: 800px;

      //       @media screen and (max-width: 900px) {
      //         left: 10px;
      //       }
      //     }
      //   }
      // }
      // &:nth-child(3) {
      //   .slide {
      //     position: relative;

      //     .video-container {
      //       left: 900px;

      //       @media screen and (max-width: 900px) {
      //         left: 10px;
      //       }
      //     }
      //   }
      // }
      // &:nth-child(4) {
      //   .slide {
      //     position: relative;
      //     .video-container {
      //       left: 950px;

      //       @media screen and (max-width: 900px) {
      //         left: 10px;
      //       }
      //     }
      //   }
      // }

      .slide {
        width: 100vw;
        min-width: 100vw;
        max-width: 100vw;
        height: 100%;
        min-height: 100%;
        display: grid;
        grid-template-columns: 45% 55%;
        align-items: center;
        padding: 20px;
        position: relative;

        @media screen and (max-width: 900px) {
          display: flex;
          flex-direction: column;
          justify-content: center;
        }

        .text {
          display: flex;
          gap: 80px;
          flex-direction: column;
          text-align: center;
          padding: 0 20px;
          margin-left: 20px;
          align-items: center;
          justify-content: center;

          @media screen and (max-width: 900px) {
            margin-top: 50px;
            gap: 40px;
            width: 80%;
            flex: 1;
          }

          @media screen and (max-width: 500px) {
            width: 100%;
          }

          h2 {
            font-size: 2.5rem;
            letter-spacing: 3px;
            font-family: $fontTR;

            @media screen and (max-width: 1000px) {
              font-size: 2.1rem;
            }

            @media screen and (max-width: 900px) {
              font-size: 2rem;
            }
          }

          p {
            font-family: $fontR;
            font-size: 1.2rem;
            line-height: 35px;

            @media screen and (max-width: 1000px) {
              font-size: 1rem;
            }
          }
        }

        .video-container {
          display: flex;
          width: 100%;
          height: 100%;
          align-items: center;
          justify-content: center;
          position: relative;

          @media screen and (max-width: 900px) {
            justify-content: center;
            align-items: baseline;
            flex: 1;
          }

          .video {
            width: 550px;
            height: 350px;
            position: relative;

            &::before {
              position: absolute;
              content: "";
              width: 90%;
              height: 0;
              background: rgba(0, 0, 0, 0.39);
              backdrop-filter: blur(0px);
              box-shadow: 0 0 20px 20px rgba(0, 0, 0, 0.4);
              border-radius: 50%;
              bottom: -40px;
              left: 50%;
              transform: translateX(-50%);
              @media screen and (max-width: 900px) {
                box-shadow: 0 0 20px 10px rgba(0, 0, 0, 0.3);
              }
            }

            @media screen and (max-width: 1100px) {
              width: 450px;
              height: 280px;
            }

            @media screen and (max-width: 1000px) {
              width: 380px;
              height: 240px;
            }

            @media screen and (max-width: 900px) {
              width: 450px;
              height: 280px;
            }

            @media screen and (max-width: 500px) {
              width: 320px;
              height: 200px;
            }

            .iframe {
              position: absolute;
              height: 100%;
              width: 100%;
              border: none;
              border-radius: 35px;

              .fullscreen {
                width: 100%;
                height: 100%;
              }
            }
          }
        }
      }
    }
  }
}
